@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,800;1,900&display=swap");

body {
	margin: 0;
	padding: 0;
	background-color: #f6fcff;
	font-family: "Montserrat", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
input::placeholder,
textarea::placeholder {
	font-family: "Montserrat", sans-serif;
	color: #aaa;
}
input:focus,
textarea:focus {
	outline: none;
}
a,
nav button {
	text-decoration: none;
	background-color: transparent;
	border: none;
	color: #000;
	transition: 0.4s;
	font-size: 1em;
	cursor: pointer;
	font-family: "Montserrat", sans-serif;
}
a:hover,
nav button:hover {
	color: #bbb;
}
form {
	max-width: 40%;
	margin: 0 auto;
}
label {
	display: flex;
	flex-direction: column;
	margin-bottom: 1em;
}
label span {
	margin-bottom: 0.3em;
	font-weight: bold;
	text-align: left;
}
input,
textarea {
	box-sizing: border-box;
	border: none;
	border-bottom: 1px solid #ccc;
	padding: 0.8em 0;
	background-color: transparent;
}
.btn {
	background-color: #721c24;
	color: #fff;
	text-align: center;
	cursor: pointer;
	border-radius: 10px;
	width: 120px;
	font-weight: bold;
	border: none;
	padding: 10px 15px;
	font-size: 1em;
}
.btn.btn-dark {
	background-color: #000;
	border-radius: 0;
}
.btn.btn-outline {
	background-color: transparent;
	border-radius: 0;
	color: #000;
	border: 1px solid #000;
	padding: 7px 30px;
}
.btn:hover{
  background-color: #721c24;
  color: #fff;
}
.btn.btn-outline:hover{
  background-color: #000;
}
.btn.btn-danger:hover{
  background-color: #DC3545;
}
button[disabled]{
  background-color: #aaa;
}
.container .error{
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  padding: 5px;
  border-radius: 5px;
}